@import '../../global/styles/variables.css';

.chart {
  width: 400px;
  max-height: 100%;
}

.bar {
  fill: var(--colorDarkBlue);
  stroke: transparent;
}


@media print {
   .bar {
     fill: var(--colorDarkBlue);
     -webkit-filter: invert(1);
     filter: invert(1);
   }
}



